.podcast
{
  display: flex;
  margin-bottom: 80px;

}
.podcast__btn
{
  width: 120px;
  height: 120px;
  border-radius: 5px;
  background-color: #26509C;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 25px;
  cursor: pointer;
  .icon
  {
    width: 64px;
    height: 64px;
  }
  @include xm
  {
    width: 80px;
    height: 80px;
    .icon
    {
      width: 48px;
      height: 48px;
    }
  }
}
.podcast__info
{
  flex: 1;
}
.podcast__top
{
  display: flex;
  align-items: center;
  margin-bottom: 13.5px;
}
.podcast__activity
{
  @include s2-d;
  padding-right: 20px;
  margin-right: 20px;
  border-right: 1px solid #000;
  @include xm
  {
    @include t1-m;
    flex: 1;
  }
}
.podcast__date
{
  @include t2-d;
  @include xm
  {
    @include t1-m;
  }
}
.podcast__name
{
  h2
  {
    @include h3-d;
  }
  @include xm
  {
    h2
    {
      @include h3-m;
    }
  }
  margin-bottom: 5px;
}
.podcast__desc
{
  @include t1-d;
  margin-bottom: 5px;
  @include xm
  {
      @include t1-m;
  }
}
.podcast__player
{
  height: 25px;
  background-color: red;
}
