.submit-modal__sogl
{
  margin-top: 20px;
  display: flex;
  align-items: center;
      @include t3-d;
  a
  {
    color: #26509C
  }
  .submit-modal__check {
    display: block;
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    height: 40px;
  }

  /* Hide the browser's default checkbox */
  .submit-modal__check input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  /* Create a custom checkbox */
  .submit-modal__check > span {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0,-50%);
    height: 20px;
    width: 20px;
    border: 3px solid #26509C;
  }

  /* On mouse-over, add a grey background color */
  .submit-modal__check:hover input ~ span {
  //  background-color: #ccc;
  }
  .submit-modal__check .radio
  {
    border-radius: 50%;
  }
  /* When the checkbox is checked, add a blue background */

  /* Create the checkmark/indicator (hidden when not checked) */
  .submit-modal__check > span:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the checkmark when checked */
  .submit-modal__check input:checked ~ span:after {
    display: block;
  }

  /* Style the checkmark/indicator */
  .submit-modal__check > span:after {
    left: 5px;
    top: 1px;
    width: 5px;
    height: 10px;
    border: solid black;
    border-width: 0 2px 2px 0;
    border-radius: 1px;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
}
